import React, { useState } from "react";
import Title from "./Title";

function App() {
  const database = [
    {
      id: 1,
      title: "хадис-1",
      text: "«Если ты не чувствуешь стыда, то делай, что хочешь»",
      show: true
    },
    {
      id:2,
      title: "хадис-2",
      text: "«Между человеком и многобожием (ширком), неверием (куфром) — оставление намаза»",
      show: true
    },
    {
      id:3,
      title: "хадис-3",
      text: "«Ни одному человеку не даровано ничего лучшего и более обширного, чем терпение»",
      show: true
    }
  ];
  
  const [baseToPrint, setBaseToPrint] = useState(database);

  const toggle = (id) => {
    setBaseToPrint (
      baseToPrint.map(item =>{
        if(item.id === id){
          return {...item, show: !item.show}
        }
        return item;
      })
    )
  }

  return (
    <div>
      {baseToPrint.map((item)=>{
        return <Title item={item} key={item.id} clickTitle={toggle} />
      })}
    </div>
  )
}

export default App;
